import React from 'react';
import Loader from '../components/Loader';
import styles from '../styles/Modal.module.css';
import { useParams } from 'react-router-dom';
import { useAppSelector } from '../hooks';

const Ingredient = () => {
  const { _id } = useParams();
  const { data } = useAppSelector((state: any) => state.dataReducer);

  if (data.length < 1) {
    return <Loader />;
  } else {
    return (
      <>
        {data
          .filter((item: any) => item._id === _id)
          .map((item: any, index: number) => {
            return (
              <section key={index}>
                <div className={styles.ingredientDetails + ' pt-10 pl-10'}>
                  <h2 className="text text_type_main-large">
                     
                  </h2>
                </div>
                <div className={styles.modalContainer}>
                  <img
                    src={item.image}
                    alt=""
                    className={styles.ingredientImage + ' mb-4'}
                  />
                  <p className="text text_type_main-medium mb-8">{item.name}</p>
                  <div className={styles.ingredientDataContainer + ' mb-15'}>
                    <div className={styles.modalContainer}>
                      <p className="text text_type_main-default text_color_inactive">
                        ,
                      </p>
                      <p className="text text_type_digits-default text_color_inactive">
                        {item.calories}
                      </p>
                    </div>
                    <div className={styles.modalContainer}>
                      <p className="text text_type_main-default text_color_inactive">
                        , 
                      </p>
                      <p className="text text_type_digits-default text_color_inactive">
                        {item.proteins}
                      </p>
                    </div>
                    <div className={styles.modalContainer}>
                      <p className="text text_type_main-default text_color_inactive">
                        , 
                      </p>
                      <p className="text text_type_digits-default text_color_inactive">
                        {item.fat}
                      </p>
                    </div>
                    <div className={styles.modalContainer}>
                      <p className="text text_type_main-default text_color_inactive">
                        , 
                      </p>
                      <p className="text text_type_digits-default text_color_inactive">
                        {item.carbohydrates}
                      </p>
                    </div>
                  </div>
                </div>
              </section>
            );
          })}
      </>
    );
  }
};

export default Ingredient;
